import React, { Component } from 'react'
import Login from './Login';
import Register from './Register';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
class User extends Component {
    componentDidMount() {
        if (this.props.auth.isAuthenticated) {
            this.props.history.push('/');
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.auth.isAuthenticated) {
            this.props.history.push('/');
        }
    }
    render() {
        return (
            <section className="profile-account-setting">
                <div className="container">
                    <div className="account-tabs-setting">
                        <div className="row">
                            <div className="col-lg-3">
                                <div className="acc-leftbar">
                                    <div className="nav nav-tabs" id="nav-tab" role="tablist">
                                        <a className="nav-item nav-link active" id="nav-acc-tab" data-toggle="tab" href="#nav-login" role="tab" aria-controls="nav-acc" aria-selected="true"><i className="la la-user"></i>Đăng nhập</a>
                                        <a className="nav-item nav-link" id="nav-status-tab" data-toggle="tab" href="#nav-register" role="tab" aria-controls="nav-status" aria-selected="false"><i className="fa fa-plane"></i>Đăng ký</a>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-9">
                                <div className="tab-content" id="nav-tabContent">
                                    <div className="tab-pane fade show active" id="nav-login" role="tabpanel" aria-labelledby="nav-acc-tab">
                                        <Login />
                                    </div>
                                    <div className="tab-pane fade" id="nav-register" role="tabpanel" aria-labelledby="nav-password-tab">
                                        <Register />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
User.propTypes = {
    auth: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    auth: state.auth,
});

export default connect(mapStateToProps, null)(User);
