import React, { Component } from 'react'
import TextFieldGroup from '../field/TextFieldGroup';
import Message from '../../utils/notificationMessage';
import Spinner from '../common/Spinner';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { registerUser } from '../../actions/authActions';
class Register extends Component {
    constructor() {
        super();
        this.state = {
            email: '',
            password: '',
            password2: '',
            name: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
            this.props.auth.loading = false;
        }
        if (nextProps.errors.message) {
            Message.displayError(nextProps.errors.message);
            this.props.auth.loading = false;
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const userData = {
            name: this.state.name,
            email: this.state.email,
            password: this.state.password,
            password2: this.state.password2,
        }

        this.props.registerUser(userData);
    }
    render() {
        const { errors } = this.state;
        const { loading } = this.props.auth;
        let loginLoading;
        if (loading) {
            loginLoading = <Spinner />
        } else {
            loginLoading = (
                <ul>
                    <li><button type="submit">Đăng ký</button></li>
                </ul>
            )
        }
        return (
            <div className="acc-setting">
                <h3>Đăng ký</h3>
                <form onSubmit={this.onSubmit}>
                    <TextFieldGroup
                        placeholder="Họ và tên"
                        name="name"
                        type="text"
                        label="Họ và tên"
                        value={this.state.name}
                        onChange={this.onChange}
                        error={errors.name}
                        icon="fa fa-user"
                    />
                    <TextFieldGroup
                        placeholder="E-mail"
                        name="email"
                        type="email"
                        label="Mật khẩu"
                        value={this.state.email}
                        onChange={this.onChange}
                        error={errors.email}
                        icon="fa fa-user"
                    />
                    <TextFieldGroup
                        placeholder="Mật khẩu"
                        name="password"
                        type="password"
                        label="Mật khẩu"
                        value={this.state.password}
                        onChange={this.onChange}
                        error={errors.password}
                        icon="fa fa-lock"
                    />
                    <TextFieldGroup
                        placeholder="Nhập lại mật khẩu"
                        name="password2"
                        type="password"
                        label="Mật khẩu"
                        value={this.state.password2}
                        onChange={this.onChange}
                        error={errors.password2}
                        icon="fa fa-lock"
                    />
                    <div className="save-stngs pd2">
                        {loginLoading}
                    </div>
                </form>
            </div>
        )
    }
}
Register.propTypes = {
    registerUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    auth: state.auth,
    errors: state.errors
});

export default connect(mapStateToProps, { registerUser })(Register);
